// Content page: Layout .handles the layout and what is not displayed in mobile
.content_page {
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    "header"
    "main-content";
}

.header {
  grid-area: header;
  svg {
    width: 50px;
  }
}
.sidebar-a {
  grid-area: sidebar-a;
  display: none;
  .sidebar-a-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    #sidebara-hr {
      display: none;
    }
  }
}
.sidebar-b {
  grid-area: sidebar-b;
  display: none;
}
.main-content {
  grid-area: main-content;
  overflow-y: scroll;
  overflow-x: hidden;
}

.main-content::-webkit-scrollbar {
  display: none;
}
