//********* breakpoints **********
// ----------------------------
// tablets
// ----------------------------
@media only screen and (min-width: 768px) {
  //Introduction Component
  .my-introduction {
    .my-introduction-container {
      height: 45%;
      span {
        font-size: 24px;
      }
      h1 {
        font-size: 45px;
      }

      p:first-of-type {
        font-size: 40px;
      }
      p:nth-of-type(2) {
        font-size: 30px;
      }
      a {
        padding: 1rem 1.5rem;
        font-size: 1.7rem;
      }
    }
  }

  // About Component
  .about-section {
    p:first-of-type {
      font-size: 30px;
    }
    p:nth-of-type(2) {
      font-size: 28px;
    }
    ul {
      li {
        font-size: 28px;
      }
    }
  }

  // Work Component
  .work-section {
    ul {
      li {
        .project {
          .project-container {
            .background-image-box {
              width: 60%;
            }
            .overlay {
              padding: 0;
              width: 60%;
              border-radius: 15px;
              .project-top {
                p {
                  background-color: rgb(29, 29, 126);
                  padding: 15px;
                }
              }
            }
          }
        }
      }
      li:nth-child(odd) .overlay {
        text-align: end;
        margin: auto 0 auto auto;
        .project-bottom {
          ul {
            justify-content: end;
          }
        }
      }
      li:nth-child(odd):hover .overlay {
        -webkit-animation: shadow-drop-2-right 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation: shadow-drop-2-right 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        padding-right: 10px;
      }
      li:nth-child(even):hover .overlay {
        -webkit-animation: shadow-drop-2-left 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        animation: shadow-drop-2-left 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
        padding-left: 10px;
      }
      li:nth-child(even) .background-image-box {
        margin-left: 40%;
      }
      li:hover .project .background-image-box {
        opacity: 1;
      }
      li:nth-child(even):hover .background-image-box {
        -webkit-animation: shadow-pop-bl 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
        animation: shadow-pop-bl 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
      }
      li:nth-child(odd):hover .background-image-box {
        -webkit-animation: shadow-pop-br 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
        animation: shadow-pop-br 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
      }
    }
  }

  .contact-section {
    h1 {
      font-size: 55px;
    }
    p {
      font-size: 28px;
      width: 85%;
      margin-bottom: 35px;
    }
    a {
      font-size: 1.7rem;
      padding: 1rem 2.5rem;
    }
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  // content page:show sidebar columns
  .content_page {
    grid-template-columns: 20% 1fr 20%;
    grid-template-areas:
      "header header header"
      "sidebar-a main-content sidebar-b";
  }

  // navbar:space nav links
  .navbar-collapse {
    justify-content: flex-end;
    ul {
      width: 80%;
      justify-content: flex-end;
      align-items: center;
      li:nth-last-child(-n + 3) {
        margin-left: 18%;
      }
    }
  }

  .about-section {
    margin-bottom: 20%;
  }

  // sidebar-a
  .sidebar-a {
    grid-area: sidebar-a;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .sidebar-a-container {
      height: 55%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;

      #sidebara-hr {
        display: flex;
      }
    }
  }
  // SidebarAshortcode
  .sidebar-a-shortcode {
    display: none;
  }

  // sidebar-b
  .sidebar-b {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    .sidebar-b-container {
      height: 55%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-end;
      a:link {
        text-decoration: none;
        color: whitesmoke;
        flex-grow: 2;
        h3 {
          font-size: 18px;
          writing-mode: vertical-rl;
          margin: 16px 0;
          color: #808080;
        }
        h3:hover {
          cursor: pointer;
          color: white;
          -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
          animation: heartbeat 1.5s ease-in-out infinite both;
        }
      }
    }
  }

  // contact section
  .contact-section {
    margin-bottom: 20%;
  }

  //hr(header rule) for sidebar A
  #sidebara-hr {
    height: 40%;
    width: 5px;
    margin-left: 22px;
    background-color: rgb(80, 80, 80);
  }

  //hr(header rule) for sidebar B
  #sidebarb-hr {
    height: 40%;
    width: 5px;
    background-color: rgb(80, 80, 80);
    margin-left: auto;
    margin-right: 10px;
  }
}
