/**
 * ----------------------------------------
 * animation slide-top-up
 * ----------------------------------------
 */
@-webkit-keyframes slide-top-up {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-100px);
    transform: translateY(-100px);
  }
}
@keyframes slide-top-up {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-100px);
    transform: translateY(-100px);
  }
}
/**
   * ----------------------------------------
   * animation slide-top-down
   * ----------------------------------------
   */
@-webkit-keyframes slide-top-down {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-100px);
    transform: translateY(-100px);
  }
}
@keyframes slide-top-down {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-100px);
    transform: translateY(-100px);
  }
}
.swirl-in-fwd {
  -webkit-animation: swirl-in-fwd 0.6s ease-out both;
  animation: swirl-in-fwd 0.6s ease-out both;
}

.swirl-out-bck {
  -webkit-animation: swirl-out-bck 0.6s ease-in both;
  animation: swirl-out-bck 0.6s ease-in both;
}

.bounce-in-top {
  -webkit-animation: bounce-in-top 1.1s both;
  animation: bounce-in-top 1.1s both;
}

.slide-top-up {
  -webkit-animation: slide-top-up 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-top-up 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  height: 0;
  visibility: hidden;
}

.slide-top-down {
  -webkit-animation: slide-top-down 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) reverse both;
  animation: slide-top-down 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) reverse both;
  height: 100%;
  visibility: visible;
}

.my-introduction {
  height: 100vh;
}

.work-section {
  margin-top: 25%;
  margin-bottom: 20%;
}

.contact-section {
  margin-bottom: 150px;
}

.about-header,
.work-header,
.contact-header {
  display: flex;
  align-items: flex-end;
}
.about-header h2:first-of-type,
.work-header h2:first-of-type,
.contact-header h2:first-of-type {
  color: rgb(255, 193, 7);
  margin: 0;
}
.about-header h2,
.work-header h2,
.contact-header h2 {
  color: white;
  margin: 0 20px 0 10px;
}
.about-header hr,
.work-header hr,
.contact-header hr {
  flex-grow: 1;
  color: #9b9b9b;
  margin-right: 10px;
}

.header {
  background-color: rgba(0, 0, 0, 0);
}
.header button {
  background-color: rgba(0, 0, 0, 0);
}
.header button i {
  color: #707070;
}

.my-introduction {
  display: flex;
  align-items: center;
}
.my-introduction .my-introduction-container {
  height: 60%;
}
.my-introduction .my-introduction-container span {
  color: #e6b543;
}
.my-introduction .my-introduction-container h1 {
  color: white;
  margin-top: 10px;
}
.my-introduction .my-introduction-container p:first-of-type {
  font-size: 26px;
  margin: 15px 0 20px 0;
}
.my-introduction .my-introduction-container p:nth-of-type(2) {
  font-size: 20px;
}
.my-introduction .my-introduction-container a {
  margin-top: 25px;
}

.about-section p:first-of-type {
  margin: 50px 0 50px 0;
  font-size: 20px;
}
.about-section p:nth-of-type(2) {
  font-size: 18px;
}
.about-section ul {
  display: flex;
  flex-wrap: wrap;
}
.about-section ul li {
  width: 50%;
  color: #707070;
}
.about-section ul li::marker {
  color: rgb(255, 193, 7);
}
.about-section .profile-picture {
  width: 60%;
  margin: 0 auto;
  --b: 0.5em;
  /* border width */
  --c: 3em;
  /* corner size */
  --r: 2em;
  /* corner rounding */
  position: relative;
  border: solid var(--b) transparent;
  padding: 1em;
  max-width: 23em;
  font: 1.25em ubuntu, sans-serif;
}
.about-section .profile-picture::before {
  position: absolute;
  z-index: -1;
  inset: calc(-1 * var(--b));
  border: inherit;
  border-radius: var(--r);
  background: linear-gradient(rgb(255, 193, 7), #ff4507, #0745ff) border-box;
  --corner: conic-gradient(from -90deg at var(--c) var(--c), red 25%, #0000 0) 0 0 / calc(100% - var(--c)) calc(100% - var(--c)) border-box;
  --inner: conic-gradient(red 0 0) padding-box;
  -webkit-mask: var(--corner), var(--inner);
  -webkit-mask-composite: source-out;
  mask: var(--corner) subtract, var(--inner);
  content: "";
}

.work-section .work-header {
  margin-bottom: 50px;
}
.work-section ul {
  list-style: none;
  padding-left: 0;
}
.work-section ul li .project {
  margin-bottom: 150px;
}
.work-section ul li .project .project-container {
  position: relative;
}
.work-section ul li .project .project-container .background-image-box {
  border-radius: 10px;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.2;
}
.work-section ul li .project .project-container .overlay {
  position: relative;
  z-index: 9;
  padding: 45px 25px 45px 25px;
}
.work-section ul li .project .project-container .overlay .project-top span {
  color: #e6b543;
}
.work-section ul li .project .project-container .overlay .project-top h3 {
  color: white;
  margin: 10px 0 20px 0;
}
.work-section ul li .project .project-container .overlay .project-top p {
  font-size: 18px;
  word-wrap: break-word;
}
.work-section ul li .project .project-container .overlay .project-bottom ul {
  display: flex;
  color: #acacac;
  margin-top: 25px;
  margin-bottom: 15px;
}
.work-section ul li .project .project-container .overlay .project-bottom ul li:nth-of-type(2) {
  margin: 0 15px;
}
.work-section ul li .project .project-container .overlay .project-bottom .project-links a svg:first-of-type {
  margin-right: 25px;
}
.work-section ul li .project:hover .background-image-box {
  opacity: 0.3;
}
.work-section ul li .project:hover .overlay {
  -webkit-animation: text-shadow-drop-center 0.6s both;
  animation: text-shadow-drop-center 0.6s both;
}

.contact-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.contact-section h1 {
  font-size: 45px;
  color: white;
  margin: 25px 0 30px 0;
}
.contact-section p {
  margin-bottom: 20px;
}
.contact-section button {
  border: solid 2px rgb(255, 193, 7);
  color: rgb(255, 193, 7);
  margin-top: 40px;
  padding: 18px 40px;
}

.sidebar-a-shortcode .sidebar-a-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.sidebar-a-shortcode .sidebar-a-container a svg {
  margin-top: 0;
}
.sidebar-a-shortcode .sidebar-a-container #sidebara-hr {
  display: none;
}

.content_page {
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas: "header" "main-content";
}

.header {
  grid-area: header;
}
.header svg {
  width: 50px;
}

.sidebar-a {
  grid-area: sidebar-a;
  display: none;
}
.sidebar-a .sidebar-a-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.sidebar-a .sidebar-a-container #sidebara-hr {
  display: none;
}

.sidebar-b {
  grid-area: sidebar-b;
  display: none;
}

.main-content {
  grid-area: main-content;
  overflow-y: scroll;
  overflow-x: hidden;
}

.main-content::-webkit-scrollbar {
  display: none;
}

.cover_page {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  overflow-y: hidden;
}
.cover_page svg {
  width: 250px;
  height: 250px;
  margin: 0;
}
.cover_page svg .path {
  stroke-dasharray: 10;
}

@media only screen and (min-width: 768px) {
  .my-introduction .my-introduction-container {
    height: 45%;
  }
  .my-introduction .my-introduction-container span {
    font-size: 24px;
  }
  .my-introduction .my-introduction-container h1 {
    font-size: 45px;
  }
  .my-introduction .my-introduction-container p:first-of-type {
    font-size: 40px;
  }
  .my-introduction .my-introduction-container p:nth-of-type(2) {
    font-size: 30px;
  }
  .my-introduction .my-introduction-container a {
    padding: 1rem 1.5rem;
    font-size: 1.7rem;
  }

  .about-section p:first-of-type {
    font-size: 30px;
  }
  .about-section p:nth-of-type(2) {
    font-size: 28px;
  }
  .about-section ul li {
    font-size: 28px;
  }

  .work-section ul li .project .project-container .background-image-box {
    width: 60%;
  }
  .work-section ul li .project .project-container .overlay {
    padding: 0;
    width: 60%;
    border-radius: 15px;
  }
  .work-section ul li .project .project-container .overlay .project-top p {
    background-color: rgb(29, 29, 126);
    padding: 15px;
  }
  .work-section ul li:nth-child(odd) .overlay {
    text-align: end;
    margin: auto 0 auto auto;
  }
  .work-section ul li:nth-child(odd) .overlay .project-bottom ul {
    justify-content: end;
  }
  .work-section ul li:nth-child(odd):hover .overlay {
    -webkit-animation: shadow-drop-2-right 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation: shadow-drop-2-right 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    padding-right: 10px;
  }
  .work-section ul li:nth-child(even):hover .overlay {
    -webkit-animation: shadow-drop-2-left 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation: shadow-drop-2-left 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    padding-left: 10px;
  }
  .work-section ul li:nth-child(even) .background-image-box {
    margin-left: 40%;
  }
  .work-section ul li:hover .project .background-image-box {
    opacity: 1;
  }
  .work-section ul li:nth-child(even):hover .background-image-box {
    -webkit-animation: shadow-pop-bl 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
    animation: shadow-pop-bl 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
  }
  .work-section ul li:nth-child(odd):hover .background-image-box {
    -webkit-animation: shadow-pop-br 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
    animation: shadow-pop-br 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
  }

  .contact-section h1 {
    font-size: 55px;
  }
  .contact-section p {
    font-size: 28px;
    width: 85%;
    margin-bottom: 35px;
  }
  .contact-section a {
    font-size: 1.7rem;
    padding: 1rem 2.5rem;
  }
}
/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .content_page {
    grid-template-columns: 20% 1fr 20%;
    grid-template-areas: "header header header" "sidebar-a main-content sidebar-b";
  }

  .navbar-collapse {
    justify-content: flex-end;
  }
  .navbar-collapse ul {
    width: 80%;
    justify-content: flex-end;
    align-items: center;
  }
  .navbar-collapse ul li:nth-last-child(-n+3) {
    margin-left: 18%;
  }

  .about-section {
    margin-bottom: 20%;
  }

  .sidebar-a {
    grid-area: sidebar-a;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .sidebar-a .sidebar-a-container {
    height: 55%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }
  .sidebar-a .sidebar-a-container #sidebara-hr {
    display: flex;
  }

  .sidebar-a-shortcode {
    display: none;
  }

  .sidebar-b {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .sidebar-b .sidebar-b-container {
    height: 55%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }
  .sidebar-b .sidebar-b-container a:link {
    text-decoration: none;
    color: whitesmoke;
    flex-grow: 2;
  }
  .sidebar-b .sidebar-b-container a:link h3 {
    font-size: 18px;
    writing-mode: vertical-rl;
    margin: 16px 0;
    color: #808080;
  }
  .sidebar-b .sidebar-b-container a:link h3:hover {
    cursor: pointer;
    color: white;
    -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
    animation: heartbeat 1.5s ease-in-out infinite both;
  }

  .contact-section {
    margin-bottom: 20%;
  }

  #sidebara-hr {
    height: 40%;
    width: 5px;
    margin-left: 22px;
    background-color: rgb(80, 80, 80);
  }

  #sidebarb-hr {
    height: 40%;
    width: 5px;
    background-color: rgb(80, 80, 80);
    margin-left: auto;
    margin-right: 10px;
  }
}

