// base
body {
  background-color: #050f26;
  overflow: hidden; /* Hide scrollbars */
}
.init_hide {
  display: none;
}

.cover_page,
.content_page {
  height: 100vh;
}

p {
  color: #acacac;
}

svg {
  width: 50px;
  fill: #808080;
}
svg:hover {
  cursor: pointer;
  fill: rgb(255, 255, 255);
  -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
  animation: heartbeat 1.5s ease-in-out infinite both;
}
