// Contact Component
.contact-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  h1 {
    font-size: 45px;
    color: white;
    margin: 25px 0 30px 0;
  }
  p {
    margin-bottom: 20px;
  }
  button {
    border: solid 2px rgb(255, 193, 7);
    color: rgb(255, 193, 7);
    margin-top: 40px;
    padding: 18px 40px;
  }
}
