// About Component
.about-section {
  p:first-of-type {
    margin: 50px 0 50px 0;
    font-size: 20px;
  }
  p:nth-of-type(2) {
    font-size: 18px;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 50%;
      color: #707070;
    }
    li::marker {
      color: rgb(255, 193, 7);
    }
  }

  .profile-picture {
    width: 60%;
    margin: 0 auto;
    --b: 0.5em; /* border width */
    --c: 3em; /* corner size */
    --r: 2em; /* corner rounding */
    position: relative;
    border: solid var(--b) transparent;
    padding: 1em;
    max-width: 23em;
    font: 1.25em ubuntu, sans-serif;

    &::before {
      position: absolute;
      z-index: -1;
      inset: calc(-1 * var(--b));
      border: inherit;
      border-radius: var(--r);
      background: linear-gradient(rgb(255, 193, 7), #ff4507, #0745ff) border-box;
      --corner: conic-gradient(from -90deg at var(--c) var(--c), red 25%, #0000 0) 0 0 / calc(100% - var(--c)) calc(100% - var(--c)) border-box;
      --inner: conic-gradient(red 0 0) padding-box;
      -webkit-mask: var(--corner), var(--inner);
      -webkit-mask-composite: source-out;
      mask: var(--corner) subtract, var(--inner);
      content: "";
    }
  }
}
