//Introduction Component
.my-introduction {
  display: flex;
  align-items: center;
  .my-introduction-container {
    height: 60%;
    span {
      color: #e6b543;
    }
    h1 {
      color: white;
      margin-top: 10px;
    }

    p:first-of-type {
      font-size: 26px;
      margin: 15px 0 20px 0;
    }
    p:nth-of-type(2) {
      font-size: 20px;
    }
    a {
      margin-top: 25px;
    }
  }
}
