// Work Component
.work-section {
  .work-header {
    margin-bottom: 50px;
  }
  ul {
    list-style: none;
    padding-left: 0;
    li {
      .project {
        margin-bottom: 150px;
        .project-container {
          // project - top area
          position: relative;
          .background-image-box {
            border-radius: 10px;
            background-repeat: no-repeat;
            background-size: cover;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0.2;
          }
          .overlay {
            position: relative;
            z-index: 9;
            padding: 45px 25px 45px 25px;
            .project-top {
              span {
                color: #e6b543;
              }
              h3 {
                color: white;
                margin: 10px 0 20px 0;
              }
              p {
                font-size: 18px;
                word-wrap: break-word;
              }
            }
            // project - bottom area
            .project-bottom {
              ul {
                display: flex;
                color: #acacac;
                margin-top: 25px;
                margin-bottom: 15px;
                li:nth-of-type(2) {
                  margin: 0 15px;
                }
              }
              .project-links {
                a {
                  svg:first-of-type {
                    margin-right: 25px;
                  }
                }
              }
            }
          }
        }
      }
      .project:hover .background-image-box {
        opacity: 0.3;
      }
      .project:hover .overlay {
        -webkit-animation: text-shadow-drop-center 0.6s both;
        animation: text-shadow-drop-center 0.6s both;
      }
    }
  }
}
